import "@glideapps/glide-data-grid/dist/index.css";
import React, { useCallback } from "react";
import DataEditor, { GridCellKind } from "@glideapps/glide-data-grid";
import { createRoot } from "react-dom/client";

export function init(ctx, data) {
  ctx.importCSS("app.css");
  ctx.importCSS("main.css");
  ctx.importCSS(
    "https://fonts.googleapis.com/css2?family=JetBrains+Mono:wght@400;500;600&display=swap"
  );
  ctx.importCSS(
    "https://cdn.jsdelivr.net/npm/remixicon@2.5.0/fonts/remixicon.min.css"
  );

  const columns = data.content.columns.map((column) => {
    return { title: column.label, id: column.key, width: 250 };
  });

  const rows = data.content.total_rows > 10 ? 10 : data.content.total_rows;

  const prev = () => {
    ctx.pushEvent("show_page", { page: data.content.page - 1 });
  };

  const next = () => {
    ctx.pushEvent("show_page", { page: data.content.page + 1 });
  };

  function Navigation() {
    return (
      <div className="navigation">
        <div class="navigation__info">
          <h2 class="navigation__name">{data.name}</h2>
          <span class="navigation__details">
            {data.content.total_rows || "?"} entries
          </span>
        </div>
        <div class="navigation__space"></div>
        {/* Actions */}
        {/* Pagination */}
        <div className="pagination">
          <button class="pagination__button" onClick={prev}>
            <i class="ri ri-arrow-left-s-line"></i>
            <span>Prev</span>
          </button>
          <div class="pagination__info">
            <span>
              {data.content.page} of {data.content.max_page || "?"}
            </span>
          </div>
          <button class="pagination__button" onClick={next}>
            <span>Next</span>
            <i class="ri ri-arrow-right-s-line"></i>
          </button>
        </div>
      </div>
    );
  }

  function App() {
    const getData = useCallback(([col, row]) => {
      const dataRow = data.content.rows[row];
      const d = Object.values(dataRow)[0][col];
      return {
        kind: GridCellKind.Text,
        data: d,
        displayData: d.toString(),
        allowOverlay: false,
      };
    }, []);

    return (
      <div className="app">
        <Navigation />
        <DataEditor
          className={"table-container"}
          theme={{
            fontFamily: "JetBrains Mono",
            bgHeader: "white",
            textDark: "#61758a",
            textHeader: "#304254",
            headerFontStyle: "bold 14px",
            baseFontStyle: "14px",
            borderColor: "#e1e8f0",
            horizontalBorderColor: "#e1e8f0",
          }}
          getCellContent={getData}
          columns={columns}
          rows={rows}
          width={896}
          rowHeight={44}
          headerHeight={44}
          verticalBorder={false}
          rowMarkers={"number"}
        />
      </div>
    );
  }

  const container = document.getElementById("root");
  const root = createRoot(container);
  root.render(<App />);
}
